@import "https://fonts.googleapis.com/css2?family=Jost:wght@300;400;500;600;700&family=Newsreader:ital,wght@0,200;0,300;0,400;0,500;0,600;1,200;1,300;1,400;1,500&display=swap";
:root {
  --pistachio: #93c572;
  --pistachio-light: #c5e0b4;
  --pistachio-dark: #618c42;
  --blue: #062d7f;
  --white: #fff;
  --gray: #f5f5f5;
  --beige: #cecbc1;
  --beige-dark: #a6a49d;
  --font-heading: "Jost", sans-serif;
  --font-body: "Newsreader", serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-body);
  color: var(--blue);
  background-color: var(--white);
  font-weight: 300;
  line-height: 1.6;
}

p, li, a:not(.btn), span, blockquote, .text-body {
  font-family: var(--font-body);
  font-weight: 300;
}

h1, h2, h3, h4, h5, h6, .section-title, .btn, .nav-links a, .nav-links button, .language-row button {
  font-family: var(--font-heading);
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 400;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

header {
  background-color: var(--white);
  z-index: 100;
  position: sticky;
  top: 0;
  box-shadow: 0 2px 10px #0000001a;
}

.navbar {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px 0 30px;
  display: flex;
  position: relative;
}

.logo {
  color: var(--blue);
  letter-spacing: 1px;
  text-transform: uppercase;
  text-align: center;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-bottom: 5px;
  font-size: 36px;
  font-weight: 500;
  line-height: 1.1;
  text-decoration: none;
  display: flex;
  font-family: Jost, sans-serif !important;
}

.logo * {
  font-family: Jost, sans-serif !important;
}

.logo-line {
  display: block;
  font-family: Jost, sans-serif !important;
}

.logo-line:first-child {
  font-size: 1.1em;
}

.logo-line:nth-child(2) {
  font-size: .9em;
}

.logo-line:last-child {
  font-size: 1em;
}

.nav-links {
  background-color: var(--white);
  z-index: 100;
  flex-direction: column;
  width: 300px;
  height: 85vh;
  padding: 70px 0 20px;
  list-style: none;
  transition: all .5s;
  display: flex;
  position: fixed;
  top: 0;
  right: -100%;
  overflow-y: auto;
  box-shadow: -5px 0 15px #0000001a;
}

.nav-links.open {
  right: 0;
}

.nav-links li {
  margin: 5px 0;
  padding-left: 30px;
  list-style: none;
}

.nav-links a {
  color: var(--blue);
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 3px 0;
  font-weight: 400;
  text-decoration: none;
  transition: color .3s;
  display: block;
}

.nav-links li:not(:last-child) {
  border-bottom: 1px solid #00000008;
  padding-bottom: 3px;
}

.nav-links a:hover {
  color: var(--beige);
}

.nav-links li button {
  cursor: pointer;
  color: var(--blue);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 400;
  font-size: inherit;
  text-align: left;
  background: none;
  border: none;
  width: 100%;
  padding: 3px 0;
  transition: color .3s;
  display: block;
}

.nav-links li button:hover {
  color: var(--beige);
}

.nav-links li button.active {
  color: var(--beige);
  text-decoration: underline;
}

.hero {
  padding: 80px 0;
  position: relative;
  overflow: hidden;
}

.hero:before {
  content: "";
  opacity: .5;
  border-radius: 50%;
  width: 300px;
  height: 300px;
  position: absolute;
  top: -50px;
  right: -50px;
}

.hero-content {
  z-index: 1;
  text-align: left;
  max-width: 600px;
  margin: 0 auto;
  position: relative;
}

.hero h1 {
  color: var(--blue);
  margin-bottom: 10px;
  font-size: 28px;
  font-weight: 200;
}

.hero h2 {
  color: var(--blue);
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 400;
}

.hero p {
  color: var(--blue);
  font-size: 16px;
  font-family: var(--font-body);
  margin-bottom: 20px;
  font-weight: 300;
  line-height: 1.7;
}

.hero-subtitle {
  margin-bottom: 20px;
  font-size: 96px;
  font-weight: 400;
}

.btn {
  background-color: var(--beige);
  color: var(--white);
  border-radius: 4px;
  padding: 12px 24px;
  font-weight: 400;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

.btn:hover {
  background-color: var(--beige-dark);
}

.text-center {
  text-align: center;
}

.text-center p {
  font-family: var(--font-body);
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.8;
}

p.description {
  font-family: var(--font-body);
  margin: 1em 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.8;
}

.long-text {
  font-family: var(--font-body);
  max-width: 70ch;
  margin: 1.5em 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.8;
}

.section {
  padding: 80px 0;
}

.section-title {
  text-align: center;
  margin-bottom: 50px;
  font-size: 24px;
  font-weight: 400;
}

.section-title:after {
  content: "";
  background-color: var(--beige);
  width: 80px;
  height: 4px;
  margin: 20px auto 0;
  display: block;
}

.card-grid {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 30px;
  display: grid;
}

.card {
  background-color: var(--white);
  border-radius: 8px;
  transition: transform .3s, box-shadow .3s;
  overflow: hidden;
  box-shadow: 0 4px 10px #0000001a;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 15px #0000001a;
}

.card-content {
  padding: 20px;
}

.card h3 {
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 400;
}

.card p {
  font-family: var(--font-body);
  color: #666;
  font-weight: 300;
  line-height: 1.7;
}

footer {
  background-color: #f9f9f9;
  margin-top: 80px;
  padding: 40px 0;
}

.footer-content {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.footer-section {
  flex: 1;
  min-width: 200px;
  margin-bottom: 20px;
}

.footer-section h3 {
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 400;
}

.footer-section ul {
  list-style: none;
}

.footer-section ul li {
  margin-bottom: 10px;
}

.footer-section a {
  color: #333;
  text-decoration: none;
  transition: color .3s;
}

.footer-section a:hover {
  color: var(--beige);
}

.footer-section p {
  font-family: var(--font-body);
  font-weight: 300;
  line-height: 1.6;
}

.footer-section:last-child p {
  font-family: var(--font-body);
  font-style: italic;
  font-weight: 300;
  line-height: 1.8;
}

.footer-bottom p {
  font-family: var(--font-body);
  font-size: 14px;
  font-weight: 300;
}

.footer-bottom {
  text-align: center;
  border-top: 1px solid #eee;
  margin-top: 40px;
  padding-top: 20px;
}

@media screen and (width <= 768px) {
  .navbar {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 12px 0 25px;
    position: relative;
  }

  .logo {
    margin-bottom: 4px;
    font-size: 20px;
    line-height: 1.1;
  }

  .logo-line {
    font-family: Jost, sans-serif !important;
  }

  .logo-line:first-child {
    font-size: 1.1em;
  }

  .logo-line:nth-child(2) {
    font-size: .9em;
  }

  .logo-line:last-child {
    font-size: 1em;
  }

  .burger-menu {
    margin-bottom: 10px;
  }

  .nav-links {
    flex-direction: column;
    margin-top: 20px;
  }

  .nav-links li {
    margin-bottom: 10px;
    margin-left: 0;
  }

  .hero h1 {
    font-size: 24px;
  }

  .section-title {
    font-size: 28px;
  }

  .footer-content {
    flex-direction: column;
  }
}

@media screen and (width <= 480px) {
  .logo {
    margin-bottom: 3px;
    font-size: 18px;
    line-height: 1.05;
  }

  .logo-line {
    font-family: Jost, sans-serif !important;
  }

  .burger-menu {
    margin-bottom: 10px;
  }

  .navbar {
    padding: 10px 0 20px;
  }
}

.burger-menu {
  cursor: pointer;
  z-index: 101;
  width: 24px;
  height: 20px;
  margin: 0 auto 10px;
  padding: 8px;
  display: block;
  position: relative;
}

.burger-bar {
  background-color: var(--blue);
  border-radius: 2px;
  width: 24px;
  height: 2.4px;
  margin: 5px 0;
  transition: all .4s;
}

.burger-menu.open {
  z-index: 102;
  margin: 0;
  position: fixed;
  top: 20px;
  right: 20px;
}

.burger-bar.open:first-child {
  background-color: var(--blue);
  transform: rotate(-45deg)translate(-5px, 6px);
}

.burger-bar.open:nth-child(2) {
  opacity: 0;
}

.burger-bar.open:nth-child(3) {
  background-color: var(--blue);
  transform: rotate(45deg)translate(-5px, -6px);
}

.menu-overlay {
  z-index: 99;
  background-color: #00000080;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.menu-overlay.open {
  display: block;
}

.language-row {
  align-items: center;
  display: flex;
}

.language-row button {
  text-align: center;
  width: auto;
  margin: 0;
  padding: 3px 0;
  font-weight: 400;
}

.language-row .separator {
  color: var(--blue);
  margin: 0 2px;
}

.image-section {
  background-color: var(--white);
  padding: 30px 0;
}

.featured-image {
  text-align: center;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
}

.featured-image img {
  border-radius: 8px;
  width: 100%;
  max-width: 100%;
  height: auto;
  box-shadow: 0 4px 10px #0000001a;
}

.text-section {
  font-family: var(--font-body);
  font-size: 18px;
  font-weight: 300;
  line-height: 1.8;
}

p, li, a:not(.btn), span, blockquote {
  font-family: var(--font-body);
}

.btn, button.btn, input[type="submit"], .nav-links a, .nav-links button, .language-row button {
  font-family: var(--font-heading);
}

input, textarea, select, option {
  font-family: var(--font-body);
  font-weight: 300;
}

.placeholder-div {
  display: none;
}
